import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { NgForm } from '@angular/forms';

import { Observable } from 'rxjs';
import { Router } from '@angular/router';
import Swal from 'sweetalert2';

import { QuestionService } from 'src/app/services/question.service';
import { Tag } from 'src/app/interfaces/question';


@Component({
  selector: 'app-form-tags',
  templateUrl: './form-tags.component.html',
  styleUrls: ['./form-tags.component.css']
})
export class FormTagsComponent implements OnInit {

  @Output() reloadTags = new EventEmitter<Tag[]>();

  tag: Tag = new Tag();
  tags: Tag[] = [];
  repeated: boolean;

  constructor(private questionService: QuestionService,
              private router: Router) { }

  ngOnInit(): void {
    this.tags = JSON.parse(localStorage.getItem("tags"));
    this.repeated = false;

    if(!this.tags){
      this.questionService.getTags()
      .subscribe( resp =>  {
        this.tags = resp;
        localStorage.setItem("tags", JSON.stringify(resp));
      });
    }

  }
  guardarTag( form: NgForm ){

    this.repeated = false;

    if( form.invalid ){
      console.log('Formulario de Tags no valido');
      return;
    }

    this.tags.forEach( element => {
      if( element.name == this.tag.name ){
        console.log("Este tag está repetido");
        this.repeated = true;
        return
      }
    });

    if(!this.repeated){
      Swal.fire({
        title: 'Espere',
        text: 'Guardando información',
        allowOutsideClick: false
      });
      Swal.showLoading();

      let peticion: Observable<any>
      peticion = this.questionService.crearTag( this.tag );
      peticion.subscribe( resp => {
        localStorage.removeItem("tags");
        this.tag.name = "";
        this.questionService.getTags()
          .subscribe( resp =>  {
            this.tags = resp;
            localStorage.setItem("tags", JSON.stringify(resp));
            this.tags = JSON.parse(localStorage.getItem("tags"));
            this.reloadTags.emit(this.tags );
          });
        Swal.fire({
          title: this.tag.name,
          text: 'Se actualizó correctamente',
        })
        //this.router.navigateByUrl('/form-data');
      });
    }



  }
}
