import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Question, Tag, Topic, User } from '../interfaces/question';
import { map, delay } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class QuestionService {

  private url = 'https://preguntas-lengua.firebaseio.com';

  constructor( private http: HttpClient) { }

  private crearArregloTag( tagsObj: object){
    const tags: Tag[] = [];
    Object.keys( tagsObj ).forEach( key => {
      const tag: Tag = tagsObj[key];
      tag.id = key;
      tags.push( tag );
    });

    if( tagsObj === null){ return []; }
    return tags;
  }
  private crearArregloQuestion( questionObj: object){
    const questions: Question[] = [];
    Object.keys( questionObj ).forEach( key => {
      const question: Question = questionObj[key];
      question.id = key;
      questions.push( question )
    });
    if( questionObj === null){ return []; }
    return questions;
  }
  private crearArregloQuestionUser( questionUserObj: object){
    const questionsUser: Question[] = [];
    Object.keys( questionUserObj ).forEach( key => {
      const questionUser: Question = questionUserObj[key];
      questionUser.id = key;
      questionsUser.push( questionUser )
    });
    if( questionUserObj === null){ return []; }
    return questionsUser;
  }
  private crearArregloTopic( topicObj: object){
    const topics: Topic[] = [];
    Object.keys( topicObj ).forEach( key => {
      const topic: Topic = topicObj[key];
      topic.id = key;
      topics.push( topic )
    });
    if( topicObj === null){ return []; }
    return topics;
  }
  private crearArregloUser( userObj: object){
    const users: User[] = [];
    Object.keys( userObj ).forEach( key => {
      const user: User = userObj[key];
      user.id = key;
      users.push( user )
    });
    if( userObj === null){ return []; }
    return users;
  }

  getUsers(){
    return this.http.get(` ${ this.url }/user.json `)
    .pipe(
      map(  this.crearArregloUser ),
      delay( 1500 )
    );
  }

  getQuestions(){
    return this.http.get(` ${ this.url }/question.json `)
    .pipe(
      map(  this.crearArregloQuestion ),
      delay( 1500 )
    );
  }
  getQuestionsUser(idUser: string ){
    return this.http.get(`${ this.url }/user/${ idUser }/question.json`)
    .pipe(
      map(  this.crearArregloQuestionUser ),
      delay( 1500 )
    );
  }

  getTopics(){
    return this.http.get(` ${ this.url }/topic.json `)
    .pipe(
      map(  this.crearArregloTopic ),
      delay( 1500 )
    );
  }

  getTags(){
    return this.http.get(` ${ this.url }/tag.json `)
    .pipe(
      map(  this.crearArregloTag ),
      delay( 1500 )
    );
  }

  getQuestion( id: string ){
    return this.http.get(`${ this.url }/question/${ id }.json`);
  }
  getTag( id: string ){
    return this.http.get(`${ this.url }/tag/${ id }.json`);
  }
  getTopic( id: string ){
    return this.http.get(`${ this.url }/topic/${ id }.json`);
  }
  getQuestionUser( idUser: string, idQuestion: string ){
    return this.http.get(`${ this.url }/user/${ idUser }/question/${ idQuestion }.json`);
  }

  /*
  getQuestionsUser( idUser: string ){
    return this.http.get(`${ this.url }/user/${ idUser }/question.json`);
  }*/


  crearTopic( topic: Topic ){
    return this.http.post(`${this.url}/topic.json`, topic)
      .pipe(
        map( (resp => {
          //topic.id = resp;
          return topic;
        })
      )
    )
  }

  crearTag( tag: Tag ){
    return this.http.post(`${this.url}/tag.json`, tag)
      .pipe(
        map( (resp => {
          //question.id = resp.;
          return tag;
        })
      )
    )
  }

  crearQuestion ( question: Question ){
    return this.http.post(`${this.url}/question.json`, question)
      .pipe(
        map( (resp => {
          //question.id = resp.;
          return question;
        })
        )
      )
  }

  crearUser( user: User ){
    return this.http.post(`${this.url}/user.json`, user)
      .pipe(
        map( (resp => {
          //topic.id = resp;
          return user;
        })
      )
    )
  }

  borrarAudio( id: string){
    return this.http.delete(`${ this.url }/question/${ id }.json`);
  }

  updateQuestionsUser( question: Question, idUser: string, idQuestion: string  ){
    return this.http.put(`${ this.url }/user/${ idUser }/question/${ idQuestion }.json`, question)
  }

  actualizarQuestion( question: Question ){
    const questionTemp = {
      ...question
    };
    delete questionTemp.id;
    return this.http.put(`${ this.url }/question/${ question.id }.json`, questionTemp);
  }
}
