import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';

import { AuthGuard } from './services/auth.guard';

import { FormDataComponent } from './pages/form-data/form-data.component';
import { ListComponent } from './pages/list/list.component';
import { TagComponent } from './pages/tag/tag.component';
import { TopicComponent } from './pages/topic/topic.component';
import { HomeComponent } from './pages/home/home.component';
import { UserComponent } from './pages/user/user.component';
import { TestsComponent } from './pages/tests/tests.component';

const routes: Routes = [
  { path: 'home', component: HomeComponent },
  { path: 'user', component: UserComponent , canActivate: [ AuthGuard ]},
  { path: 'tests', component: TestsComponent , canActivate: [ AuthGuard ]},
  { path: 'tests-topic/:id', component: TestsComponent , canActivate: [ AuthGuard ]},
  { path: 'tests-tags/:id', component: TestsComponent , canActivate: [ AuthGuard ]},
  { path: 'list', component: ListComponent , canActivate: [ AuthGuard ]},
  { path: 'form-data/:id', component: FormDataComponent, canActivate: [ AuthGuard ]},
  { path: 'tag/:id', component: TagComponent, canActivate: [ AuthGuard ]},
  { path: 'topic/:id', component: TopicComponent, canActivate: [ AuthGuard ]},
  { path: '**', pathMatch: 'full', redirectTo: 'home' }
]


@NgModule({
  imports: [
    CommonModule,
    RouterModule.forRoot(routes)
  ],
  exports: [RouterModule]
})
export class AppRoutingModule { }
