export class Question {
  id: string;
  number: number;
  topic: any;
  title: string;
  question: string;
  correct: boolean;
  n_questions: number;
  n_corrects: number;
  note: string;
  tags: Tag[];
}
export class Tag{
  id: string;
  name: string;
  number: number;
}
export class Topic{
  id: string;
  name: string;
  numberName: number;
  number: number;
}
export class User{
  id: string;
  email: string;
  questions: Question[]
}
